import React from 'react';
import { Flex, Box } from 'rebass';
import styled from '@emotion/styled/macro';

const Diamond = styled(Box)`
  position: relative;
  height: 100%;
  width:  100%;
  line-height: 3.5;
  text-align: center;

  svg {
    position: absolute;
    top: 0px;
    left: 0px;
    height: 100%;
    width: 100%;
    z-index: -1;
  }

  path {
    fill: inherit;
    stroke: transparent;
    -webkit-filter: url(#dropshadow);
    filter: url(#dropshadow);
  }
`

const Badge = (props) => {
  const {
    text,
    format = 'box',
    size = 50,
    width = ['20vw', '5vw'],
    height = ['10vw', '3vw'],
    textColor = 'white',
    bgColor = 'badge',
    fill = bgColor,
    mx,
    my,
  } = props;

  let borderRadius;
  if (format === 'circle') {
    borderRadius = 9999;
  } else if (format === 'box') {
    borderRadius = 0;
  }

  return (
    <Flex
      color={textColor}
      alignItems="center"
      justifyItems="center"
      justifyContent="center"
      mx={mx}
      my={my}
      sx={{
        display: 'inline-block',
        width,
        height,
        borderRadius,
        fill,
      }}
    >
      
      <Diamond fontWeight="heading" fontSize={['2.5vw', '.8vw']}>
        <svg viewBox='0 0 100 100' preserveAspectRatio='none'>
          <filter id="dropshadow" height="125%">
            <feGaussianBlur in="SourceAlpha" stdDeviation="1" />
            <feOffset dx="0" dy="0" result="offsetblur" />
            <feMerge>
              <feMergeNode/>
              <feMergeNode in="SourceGraphic" />
            </feMerge>
          </filter>
          <path d='M2,50 50,2 98,50 50,98z' />
        </svg>
        {text}
      </Diamond>
    </Flex>
  );
};

export default React.memo(Badge);
